import { useState } from "react";
import { nanoid } from 'nanoid'

import s from "./CategoriesForm.module.css";

import addIcon from "../../assets/icons/add.svg";

import { useDispatch,useSelector } from "react-redux";
import { add } from "../../redux/categories/categoriesSlice";
import { getFilter } from "../../redux/filter/filterSelectors";

const CategoriesForm = () => {
  const [nameCategory, setNameCategory] = useState("");
  const dispatch = useDispatch();
  const filter = useSelector(getFilter);

  const handleChange = (e) => {
    const { value } = e.target;
    setNameCategory(value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (nameCategory === "") {
      return alert("Please enter category");
    }

    const newCategory = {id: nanoid(), transaction: filter, nameCategory}
    dispatch(add(newCategory));
    setNameCategory("");
  };

  return (
    <form
      onSubmit={handleSubmit}
      name="add_category"
      autoComplete="off"
      noValidate
      className={s.addItemForm}
    >
      <label className={s.addItemLabel}>
        <input
          type="text"
          name="category"
          placeholder="New category..."
          className={s.addItemInput}
          value={nameCategory}
          onChange={handleChange}
        />
      </label>

      <button type="submit" className={s.btnAdd}>
        <img src={addIcon} alt="icon Add" />
      </button>
    </form>
  );
};

export default CategoriesForm;
